}
@media (max-width: 767px) {
  .page-header__wrapper {
    padding: 0 0 0 20px;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    padding: 0 30px;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    min-height: 165px;
