    position: absolute;
    display: block;
    width: 175px;
    height: 52px;
    padding-top: 24px;
    left: 0;
  }
}
@media (min-width: 1440px) {
  .page-header__logo {
    position: absolute;
    display: block;
    padding-top: 54px;
    left: 0;
    width: 202px;
    height: 59px;
  }
}
.main-nav {
  position: relative;
  width: auto;
  min-height: 66px;
  border-bottom: 1px solid #D9D9D9;
}
@media (min-width: 768px) {
  .main-nav {
    border-bottom: none;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__wrapper {
  position: relative;
  padding-top: 25px;
  margin-bottom: -2px;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    padding: 0 0 0 30px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    margin: 0;
    padding-right: 0;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .main-nav__logo::before {
    position: absolute;
    content: "";
    left: 20px;
    margin-top: -12px;
    background-image: url("../img/logo-mobile.svg");
    background-size: cover;
    background-repeat: no-repeat;
    width: 33px;
    height: 37px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 60px;
  height: 66px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 20px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item,
  .main-nav--closed .site-list__item-index {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 34px;
  right: 20px;
  width: 18px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
